<template>
  <div id="app">
    <div v-if="$apollo.loading">Loading..</div>
    <!-- <div v-else>
      <h1>Name: {{viewer.result.result.resources}}</h1>
    </div>  -->
    <div class="container">
      <router-view/>
    </div>
  </div>
</template>

<script>
import gql from 'graphql-tag';
import Vue from 'vue'
import VuePapaParse from 'vue-papa-parse'
Vue.use(VuePapaParse)

import { mapGetters, mapActions } from "vuex";

export default {
  data: function() {
    return {
      viewer: {},
      allStation: {},
      infoAboutStation: {},
      ecologyArrayAll: [],
      prunedTrees: [],
      newTrees: [],
      removedTrees: []
    };
  },
  apollo: {
    viewer: {
      // https://data.gov.ua/dataset/5ec243d8-3a0a-4bf3-9051-2c61196200e4
      // Дані про зелені насадження, що підлягають видаленню, відповідно до виданих актів обстеження зелених насаджень
      query: gql`
        query {
          result @rest(type: "PlantsPayload", path: "api/3/action/package_show?id=5ec243d8-3a0a-4bf3-9051-2c61196200e4") {
            result @type(name: "PlantsArrayInfo") {
              resources @type(name: "PlantsArrayUrl") {
                url
              }
            }
          }
        }`,
      update(data) {
        return data;
      }
    },
    allStation: {
      // https://data.gov.ua/dataset/e30e47e2-8fd0-4202-a5d4-20f07cfd116c
      // Інформація про атомні електростанції
      query: gql`
        query {
          result @rest(type: "StationPayload", path: "api/3/action/package_show?id=e30e47e2-8fd0-4202-a5d4-20f07cfd116c") {
            result @type(name: "StationArrayInfo") {
              resources @type(name: "StationArrayUrl") {
                url
              }
            }
          }
        }`,
      update(data) {
        return data;
      }
    },
    infoAboutStation: {
      // https://data.gov.ua/dataset/4a9d3d56-bd95-4c3e-97e7-1cdc7bcbd445
      // Екологічна та радіаційна обстановка в зоні розташування атомних електростанцій
      query: gql`
        query {
          result @rest(type: "CurrentStationPayload", path: "api/3/action/package_show?id=4a9d3d56-bd95-4c3e-97e7-1cdc7bcbd445") {
            result @type(name: "CurrentStationArrayInfo") {
              resources @type(name: "CurrentStationArrayUrl") {
                url
              }
            }
          }
        }`,
      update(data) {
        return data;
      }
    }
  },
  created() {
    console.log(this.viewer, 'viewer')
    console.log(this.allStation, 'allStation')
    console.log(this.infoAboutStation, 'infoAboutStation')
  },
  computed: {
    ...mapGetters([
      'getPrunedTreesArray',
      'getNewTreesArray',
      'getRemovedTreesArray'
    ]),
  },
  watch: {
    viewer: function (value) {
      console.log(value, 'ecology')
      let array = value.result.result.resources.map(x => x.url)
      array.forEach(element => {
        if (element.substr(-3) !== 'csv') {
          return
        }
        if (element.indexOf('obrizkazn') !== -1) {
          this.prunedTrees.push(element)
        }
        if (element.indexOf('vysadzennjazn') !== -1) {
          this.newTrees.push(element)
        }
        if (element.indexOf('vydalennja') !== -1) {
          this.removedTrees.push(element)
        }
      })
      this.savePrunedTrees()
      this.saveNewTrees()
      this.saveRemovedTrees()
    },
    allStation: function (value) {
      console.log(value, 'allStation')
    },
    infoAboutStation: function (value) {
      console.log(value, 'infoAboutStation')
    }
  },
  methods: {
    ...mapActions([
      'pushArrayPronudedTrees',
      'pushArrayNewTrees',
      'pushArrayRemovedTrees'
    ]),
    savePrunedTrees () {
      this.prunedTrees.forEach(element => {
        this.$papa.parse("https://cors-anywhere.herokuapp.com/"+element, {
            download: true,
            header: true,
            complete: (results) => {
              results.data.forEach((element) => {
                if (element.latitude && element.longitude) {
                  this.pushArrayNewTrees({
                    lat: element.latitude, 
                    lan: element.longitude,
                    orderIdentifier:  element.orderIdentifier,
                    type: element.itemSpecies,
                    status: element.status,
                    sender: element.senderName,
                    contractor: element.orderContractorLegalName,
                    date: element.orderDate
                  })
                }
              }); 
            }
        })
      });
      // console.log(this.getPrunedTreesArray, 'getPrunedTreesArray')
    },
    saveNewTrees () {
      this.newTrees.forEach(element => {
        this.$papa.parse("https://cors-anywhere.herokuapp.com/"+element, {
            download: true,
            header: true,
            complete: (results) => {
              // console.log(results, 'results');
              results.data.forEach((element) => {
                if (element.latitude && element.longitude) {
                    this.pushArrayPronudedTrees({
                    lat: element.latitude, 
                    lan: element.longitude,
                    orderIdentifier:  element.orderIdentifier,
                    type: element.itemSpecies,
                    status: element.status,
                    sender: element.senderName,
                    contractor: element.orderContractorLegalName,
                    date: element.orderDate
                  })
                }
              }); 
            }
        })
      });
      console.log(this.getNewTreesArray, 'getNewTreesArray')
    },
    saveRemovedTrees () {
      this.removedTrees.forEach(element => {
        this.$papa.parse("https://cors-anywhere.herokuapp.com/"+element, {
            download: true,
            header: true,
            complete: (results) => {
              results.data.forEach((element) => {
                if (element.latitude && element.longitude) {
                  this.pushArrayRemovedTrees({
                    lat: element.latitude, 
                    lan: element.longitude,
                    orderIdentifier:  element.orderIdentifier,
                    type: element.itemSpecies,
                    status: element.status,
                    sender: element.senderName,
                    contractor: element.orderContractorLegalName,
                    date: element.orderDate
                  })
                }           
              }); 
            }
        })
      });
      console.log(this.getRemovedTreesArray, 'getRemovedTreesArray')
    }
  },
};
</script>

